import React from 'react';
import WordInput from './WordInput.js'
import Result from './Result.js'
import './App.css';

class App extends React.Component {

  state = {
    words: ["a","b","c"],
    vectors: NaN,
    response: ""
  }

  constructor(props) {
    super(props);
    this.state.vectors = require("./vectors.json")
    
  }

  changeWord = (word,pos) => {
    // this.setState({words: ["bish","bash","bosh"]});
    console.log("test2")
    this.setState(this.replaceWord(this.state,word,pos))
  }

  vectorDistance = (v1,v2) =>{
    var distance = 0.0
    for (var i = 0; i < 300; i++) { 
      distance += (v1[i]-v2[i]) * (v1[i]-v2[i])
    }

    return Math.sqrt(distance)
  }

  calculateResponse = (words) =>{

    for(i =0; i < 3; i++){
      if(!this.state.vectors[words[i]]){
        return "no vec for " + words[i]
      }
    }

    console.log(words)
    var v1 = this.state.vectors[words[0]]
    var v2 = this.state.vectors[words[1]]
    var v3 = this.state.vectors[words[2]]

    var vResult = []

    for (var i = 0; i < 300; i++) { 
      vResult.push(-v1[i]+v2[i]+v3[i])
    }

    var minDistance = 20000000
    var minWord = ""
    var d = 0
    const entries = Object.entries(this.state.vectors)
    for (const entry of entries) {
      d = this.vectorDistance(vResult,entry[1])
      if (d< minDistance){
        minDistance = d
        minWord = entry[0]
        console.log(minWord)
      }
    }

    

    return minWord

    
  }

  replaceWord(obj,word,pos){
    var values =obj.words
    values[pos] = word
    return{
      words: values,
      response: this.calculateResponse(values)
    }
  }

  render() {
    return (
      <div className="App">
      <header className="App-header">
        Word2Vec analogy search
      </header>
      <div className="Inputs">
        <div className = "top">       
          <WordInput handler={this.changeWord} pos={0}/>
          is to
          <WordInput handler={this.changeWord} pos={1}/>
        </div>
        as
        <div className = "bottom">
          <WordInput handler={this.changeWord} pos={2}/>
          is to
          <Result response={this.state.response}/>
        </div>


      </div>
    </div>
     
    );
  }
}
export default App;
