import React from 'react';
import './App.css';

class WordInput extends React.Component {

    constructor(props) {
      super(props);
    }

    handleChange = (event) => {
      console.log("change")
      this.props.handler(event.target.value,this.props.pos)           
    }
  
    render() {
      return (
          <div className = "word-input" onChange={this.handleChange}>
            <input className = "input"/>
          </div>
       
      );
    }
  }
export default WordInput;